import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { ReactComponent as ArrowLeft } from "../assets/arrow-left.svg";
import { AppHeader } from "../components/Header";

const NotePage = ({ match, history }) => {
  const { id } = useParams();
  let [note, setNote] = useState({ body: "", create: "", id: "", update: "" });
  const navigate = useNavigate();
  useEffect(() => {
    getNote();
  }, []);

  const getNote = async () => {
    if (id === "new") return;

    let response = await fetch(`/api/notes/${id}/`);
    let data = await response.json();
    // console.log('data', data)
    setNote(data);
  };

  let createNote = async () => {
    fetch(`/api/notes/`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(note),
    });
  };

  let updateNote = async () => {
    fetch(`/api/notes/${id}/`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(note),
    });
  };

  let deleteNote = async () => {
    var name=prompt("Please enter the password");
    if (name==="1234")
    fetch(`/api/notes/${id}/`, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
      },
    });
    navigate("/news");
  
    
  };

  let handleSubmit = () => {
    if (id !== "new" && note.body === "") {
      deleteNote();
    } else if (id !== "new") {
      updateNote();
    } else if (id === "new" && note.body !== null) {
      createNote();
    }
    navigate("/news");
  };

  let handleChange = (value) => {
    setNote((note) => ({ ...note, body: value }));
    console.log("Handle Change:", note);
  };



  function validateUser(){
    var name=prompt("Please enter the password");
    if (name==="1234")
    document.getElementsByName("comment")[0].disabled=false;
  }
  return (
    
    <div className="note">
             <AppHeader />
      <div className="note-header">
        <h3>
          <ArrowLeft onClick={handleSubmit} />
        </h3>
        {/* {id !== "new" ? (
          <button onClick={deleteNote}>Delete</button>
        ) : (
          <button onClick={handleSubmit}>Done</button>
        )} */}
      </div>
      {/* <textarea
        onChange={(e) => {
          setNote({ ...note, body: e.target.value });
        }}
        defaultValue={note.body}
      ></textarea> */}
      <button onClick={validateUser} >Edit</button>
      <button onClick={deleteNote} >DELTE</button>
      <textarea name="comment" cols="5" rows="2" disabled="disabled" onChange={(e) => { handleChange(e.target.value) }} value={note.body}></textarea>  
      
    </div>
  );
};

export default NotePage;
